@import url('https://fonts.googleapis.com/css2?family=Archivo:wght@500&display=swap');



:root {
    --color-primary: #FFFFFF;
    --color-secondary: #EE5F20;
    --color-accent: #F4F1ED;
    --color-body: #31211B;
    --text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

}

*, *::after, *::before {
    box-sizing: border-box;
}

/* Typography */

::selection {
    background: var(--color-primary);
    color: #fff;
}

html {
    font-size: 62.5%;
    height: 100%;
    width: 100%;
}
main {
  height: 100%;
  width: 100%;
}

img {
    width: 100%;
}

body {
    font-family: 'Archivo', Arial, Helvetica, sans-serif;
    font-size: 2rem;
    line-height: 1.5;
    color: var(--color-body);
}

h1, h2, h3 {
    color: var(--color-headings);
    margin-bottom: 1rem;
    line-height: 1.1;

}

h1 {
    font-size: 4.1rem;
}

h2 {
    font-size: 3.2rem;
}

h3 {
    font-size: 1.8rem;
    font-weight: 600;
    line-height: 2.0;
    font-style: normal;
    font-weight: 500;

}

p {
    margin-top: 0;
}


a {
    text-decoration: none;
    color: #000000;
}

label {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 15px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #31211B;
}